'use strict';

var pkg = require('../../../package.json');

console.log(pkg);

class VersionLabel {
    constructor() {
        this.$el = window.document.createElement('i');
        this.$el.classList.add('executor-version-label');
        this.$el.innerHTML = pkg.version;
    }
}

export default VersionLabel;



